.header {
  min-height: 70px;
  display: flex;
  align-items: center;
  position: relative;
}

.mobile-header {
  display: flex;
  justify-content: space-between;
  align-items: center;

  @include media('>tablet') {
    display: none;
  }

}

.hamburger-holder {
  display: block;

  .hamburger {
    position: relative;
    width: 30px;
    height: 20px;
    cursor: pointer;
    /*&.active {
      .line1 {
        transform: translate(-50%, -50%), rotate(45deg);
      }

      .line3 {
        transform: translate(-50%, -50%) rotate(-45deg);
      }
    }*/

    .line {
      position: absolute;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 100%;
      height: 2px;
      background: #000;
      transition: transform 0.3s;
    }

    .line1 {
      top: 0;
    }

    .line2 {
      top: 50%;
    }
    .line3 {
      top: 100%;
    }

    &.active {
      .line1 {
        transform: translate(-50%, -50%) rotate(45deg);
        top: 50%;
      }

      .line2 {
        display: none;
      }

      .line3 {
        transform: translate(-50%, -50%) rotate(-45deg);
        top: 50%;
      }
    }
  }
}

.header-holder {
  display: none;
  padding: 20px;
  position: absolute;
  left: 0;
  top: 100%;
  width: 100%;

  @include media('>tablet') {
    position: relative;
    left: auto;
    top: auto;
    display: flex;
    align-content: center;
    justify-content: space-between;
    padding: 0;
  }

  &.active {
    display: block;
    z-index: 999;
    background: #fff;
    height: 100vh;
  }

  .logo{
    display: none;
    @include media('>tablet') {
      display: flex;
    }
  }

  .social_list {
    li {
      padding: 5px 10px;
      color: #20d07f;
    }

    .header-phone {
      img {
        margin: 0 10px 0 0;
      }
    }
  }

}

.logo {
  display: flex;
  align-items: center;

  strong {
    padding: 0 0 0 10px;
  }
}

.menu-holder {
  display: flex;
  align-items: center;
}

.menu {
  display: block;

  @include media('>tablet') {
    display: flex;
  }
  li {
    padding: 10px 13px;

    @include media('>tablet'){
      padding: 0 13px;
    }

    a {
      text-transform: uppercase;
      font-size: 12px;
      transition: color 0.3s;
      &:hover {
        color: #FFA300;
      }
    }
  }
}

.contacts {
  display: flex;
  align-items: center;
  font-size: 16px;
  padding: 10px 0;

  @include media('>tablet') {
    padding: 0;
  }
}

.social_list {
  display: flex;
  align-items: center;
  li {
    padding: 5px;

    a {
      opacity: 1;
      transition: opacity 0.3s;
      &:hover {
        opacity: 0.5;
      }
    }
  }
}